.annapurna {
  .banner-2 {
    background: no-repeat right top/contain url("../../images/bg-2.svg");
    //   height: 400px;

    @media (max-width: $container-width-tablet) {
      background: no-repeat right top/cover url("../../images/bg-2-tablet.svg");
      // height: 349px;
    }

    @media (max-width: $container-width-mobile) {
      background: no-repeat right top/cover url("../../images/bg-2-mobile.svg");
      // height: 583px;
    }

    .row {
      padding: 80px 0px 208px 0px;

      @media (max-width: $container-width-tablet) {
        padding: 80px 0px 72px 0px;
      }

      @media (max-width: $container-width-mobile) {
        padding: 80px 0px 291px 0px;
      }
    }

    .col-10:first-child {
      max-width: 435px;
    }

    @media (max-width: $container-width-tablet) {
      .col-md-15 {
        max-width: 392px !important;
      }
    }
  }
}
