.information-disclosure {
  .ac {
    padding: 24px;
    max-width: 677px;

    .ac-trigger div {
      color: $light-grey;
      opacity: 0.7;
      font-weight: 400;
      font-size: 18px;
    }

    .ac-trigger::after {
      background: no-repeat right top / cover
        url(../../images/arrow-down--grey.svg);

      height: 24px;
      width: 24px !important;
    }
  }
  .ac.is-active .ac-trigger::after {
    background: no-repeat right top / cover url(../../images/arrow-up--grey.svg);
  }

  .select-information {
    background: no-repeat right top / cover
        url("../../images/info-disclosure-bg.svg"),
      linear-gradient(
        119.25deg,
        rgba(20, 19, 36, 0) 32.866%,
        rgba(20, 19, 36, 0.75) 64.453%,
        rgba(20, 19, 36, 0.9) 73.606%,
        rgb(20, 19, 36) 80.197%
      );
    min-height: 618px;
    margin-top: -58px;

    @media (max-width: $container-width-tablet) {
      background: unset;
    }

    h1 {
      margin-top: calc(160px + 58px);
      background: linear-gradient(154.57deg, #316ced 7.12%, #efefef 28.636%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;

      margin-bottom: 80px;
    }
  }
}
