.contacts {
  .contacts-wrapper {
    margin-top: -67px;
    padding: 242px 0 80px 0;

    @media (max-width: $container-width-desktop) {
      background: no-repeat right top / cover
        url("../../images/contacts-background.svg");
    }

    @media (max-width: $container-width-tablet) {
      padding: 146px 0 240px 0;
    }
    @media (max-width: $container-width-mobile) {
      padding: 146px 0 226px 0;
    }
    .contacts-title {
      background: linear-gradient(154.57deg, #316ced 7.12%, #efefef 28.636%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      margin-bottom: 64px;
      margin-top: 67px;
    }

    .subtitle {
      margin-bottom: 16px;
    }
    .content {
      margin-bottom: 40px;
    }
  }
}
